import React, { Component } from "react";
import ContentBase from "./../components/contentBase";
import Status from "./TestStatus";
import Card from "./../components/card";
import $ from "jquery";
import Square from "./../components/square";
import Timer from "./../components/timer";

class FocusTest extends Component {
  state = {
    status: Status.START,
    username: this.props.username,
    isLogin: this.props.isLogin,
    timeIntervals: [],
    averTime: 0,
    score: 0,
    isUpload: false,
    clickCount: 0,
    isCheat: false,
  };

  startTest = () => {
    this.setState({
      status: Status.RUNNING,
      timeIntervals: [],
      averTime: 0,
      score: 0,
      isUpload: false,
      clickCount: 0,
      isCheat: false,
    });
  };

  cancelTest = () => {
    window.location.href = "/";
  };

  continueTest = () => {
    this.setState({
      status: Status.START,
    });
  };

  calculateScore = (aver_time) => {
    // aver_time0-1
    const normalizedTime = Math.min(Math.max(aver_time / 1000, 0), 1);

    // normalizedTime0-10
    const score = Math.floor(10 * (1 - normalizedTime));

    return score;
  };

  handleClick = () => {
    this.setState((prevState) => ({ clickCount: prevState.clickCount + 1 }));
  };

  isCheating(changeCount) {
    const { clickCount } = this.state;
    return clickCount >= 2 * changeCount;
  }

  handleSubmit = () => {
    console.log(this.state.timeIntervals);
    console.log("");

    const times = this.state.timeIntervals;

    var sum_time = 0;
    var len = times.length - 1;
    for (let index = 0; index < times.length - 1; index++) {
      const element = times[index];
      sum_time += element;
    }

    var back = times.pop();
    if (back !== 1000) {
      sum_time += back;
      len += 1;
    }

    var aver_time = sum_time / len;

    console.log(aver_time);

    var score = this.calculateScore(aver_time);
    console.log(score);

    var cheat = this.isCheating(len);

    this.setState({
      timeIntervals: [],
      status: Status.END,
      score: score,
      averTime: aver_time,
      isCheat: cheat,
    });
  };

  uploadScore = () => {
    this.setState({ isUpload: true });
    $.ajax({
      url: "https://tw-luo-opulent-goldfish-w546v5j77gh56xj-8000.preview.app.github.dev/game/api/add_score/",
      type: "get",
      data: {
        test_type: "",
        score: this.state.score,
      },
      dataType: "json",
      success: (resp) => {
        console.log(resp);
        if (resp.result === "success") {
          alert('');
        } else {
          alert("");
        }
      },
    });
  };

  game() {
    if (this.state.status === Status.START) {
      return (
        <Card>
          <div className="testTitle"></div>
          <pre className="testInstruction">
            {
              "\n\n\n\n"
            }
          </pre>
          <div className="row">
            <div className="col-sm-2">
              <button className="btn btn-primary" onClick={this.startTest}>
                
              </button>
            </div>
            <div className="col-sm-2">
              <button className="btn btn-danger" onClick={this.cancelTest}>
                
              </button>
            </div>
          </div>
        </Card>
      );
    } else if (this.state.status === Status.RUNNING) {
      return (
        <React.Fragment>
          <ContentBase>
            <Timer time={10} onTimeUp={this.handleSubmit}></Timer>
            <Square
              timeIntervals={this.state.timeIntervals}
              click={this.handleClick}
            ></Square>
          </ContentBase>
        </React.Fragment>
      );
    } else {
      if (this.state.isCheat === true) {
        return (
          <Card>
            <div className="testTitle"></div>
            <pre className="testInstruction">
              
            </pre>
            <div className="row">
              <div className="col-sm-2">
                <button className="btn btn-primary" onClick={this.continueTest}>
                  
                </button>
              </div>
              <div className="col-sm-2">
                <button className="btn btn-danger" onClick={this.cancelTest}>
                  
                </button>
              </div>
            </div>
          </Card>
        );
      } else {
        if (this.state.isUpload === false) {
          this.uploadScore();
        }
        return (
          <Card>
            <div className="testTitle"></div>
            <pre className="testInstruction">
              {` ${this.state.score} `}
            </pre>
            <div className="row">
              <div className="col-sm-2">
                <button className="btn btn-primary" onClick={this.continueTest}>
                  
                </button>
              </div>
              <div className="col-sm-2">
                <button className="btn btn-danger" onClick={this.cancelTest}>
                  
                </button>
              </div>
            </div>
          </Card>
        );
      }
    }
  }

  test() {
    if (this.state.isLogin === false) {
      return <Card></Card>;
    } else {
      return this.game();
    }
  }

  render() {
    return (
      <ContentBase>
        <Card style={{ marginBottom: "20px" }}>
          <h2></h2>
        </Card>
        {this.test()}
      </ContentBase>
    );
  }
}

export default FocusTest;
