import React, { Component } from "react";
import ContentBase from "./../components/contentBase";
import Status from "./TestStatus";
import Card from "./../components/card";
import $ from "jquery";
import Timer from "./../components/timer";
import Board from "../components/board";

class ReflexTest extends Component {
  state = {
    status: Status.START,
    username: this.props.username,
    isLogin: this.props.isLogin,
    score: 0,
    isUpload: false,
    isCheat: false,
    clickCount: 0,
  };

  startTest = () => {
    this.setState({
      status: Status.RUNNING,
      score: 0,
      isUpload: false,
      isCheat: false,
      clickCount: 0,
    });
  };

  cancelTest = () => {
    window.location.href = "/";
  };

  continueTest = () => {
    this.setState({
      status: Status.START,
    });
  };

  handleClick = () => {
    this.setState((prevState) => ({ clickCount: prevState.clickCount + 1 }));
  };

  isCheating(changeCount) {
    const { clickCount } = this.state;
    return clickCount >= 2 * changeCount;
  }

  countNumbersLessThan = (numbers, cnt) => {
    let count = 0;
    for (let i = 0; i < numbers.length; i++) {
      if (numbers[i] < cnt) {
        count++;
      }
    }
    return count;
  };

  handleSubmit = () => {
    const { clickCount } = this.state;
    console.log("");
    console.log(clickCount);

    const min = 10;
    const max = 30;
    var count = 100;

    const numbers = [];
    for (let i = 0; i < count; i++) {
      const number = Math.random() * (max - min) + min;
      numbers.push(number);
    }

    count = this.countNumbersLessThan(numbers, clickCount);

    var score = Math.floor(count / 10);

    this.setState({
      status: Status.END,
      score: score,
    });
  };

  uploadScore = () => {
    this.setState({ isUpload: true });
    $.ajax({
      url: "https://tw-luo-opulent-goldfish-w546v5j77gh56xj-8000.preview.app.github.dev/game/api/add_score/",
      type: "get",
      data: {
        test_type: "",
        score: this.state.score,
      },
      dataType: "json",
      success: (resp) => {
        console.log(resp);
        if (resp.result === "success") {
          alert("");
        } else {
          alert("");
        }
      },
    });
  };

  game() {
    if (this.state.status === Status.START) {
      return (
        <Card>
          <div className="testTitle"></div>
          <pre className="testInstruction">
            {
              " 9 \n\n"
            }
          </pre>
          <div className="row">
            <div className="col-sm-2">
              <button className="btn btn-primary" onClick={this.startTest}>
                
              </button>
            </div>
            <div className="col-sm-2">
              <button className="btn btn-danger" onClick={this.cancelTest}>
                
              </button>
            </div>
          </div>
        </Card>
      );
    } else if (this.state.status === Status.RUNNING) {
      return (
        <React.Fragment>
          <ContentBase>
            <Timer time={10} onTimeUp={this.handleSubmit}></Timer>
            <Board handleClick={this.handleClick}></Board>
          </ContentBase>
        </React.Fragment>
      );
    } else {
      if (this.state.isCheat === true) {
        return (
          <Card>
            <div className="testTitle"></div>
            <pre className="testInstruction">
              
            </pre>
            <div className="row">
              <div className="col-sm-2">
                <button className="btn btn-primary" onClick={this.continueTest}>
                  
                </button>
              </div>
              <div className="col-sm-2">
                <button className="btn btn-danger" onClick={this.cancelTest}>
                  
                </button>
              </div>
            </div>
          </Card>
        );
      } else {
        if (this.state.isUpload === false) {
          this.uploadScore();
        }
        return (
          <Card>
            <div className="testTitle"></div>
            <pre className="testInstruction">
              {` ${this.state.score} `}
            </pre>
            <div className="row">
              <div className="col-sm-2">
                <button className="btn btn-primary" onClick={this.continueTest}>
                  
                </button>
              </div>
              <div className="col-sm-2">
                <button className="btn btn-danger" onClick={this.cancelTest}>
                  
                </button>
              </div>
            </div>
          </Card>
        );
      }
    }
  }

  test() {
    if (this.state.isLogin === false) {
      return <Card></Card>;
    } else {
      return this.game();
    }
  }

  render() {
    return (
      <ContentBase>
        <Card style={{ marginBottom: "20px" }}>
          <h2></h2>
        </Card>
        {this.test()}
      </ContentBase>
    );
  }
}

export default ReflexTest;
