import React, { Component } from "react";
import ContentBase from './contentBase';

class Home extends Component {
  state = {};
  
  getData(){
    if(this.props.isLogin){
      return (
        <React.Fragment>
          <div>{this.props.username+""}</div>
          <div></div>
        </React.Fragment>
      )
      
      
    }else{
      return ""
    }
  }

  render() {
    return (
        <ContentBase>{this.getData()}</ContentBase>
    );
  }
}

export default Home;
